<template>
  <card-wrap>
    <div class="resume-card">
      <div v-if="title" class="resume-card__title">
        {{ title }}
      </div>
      <div class="resume-card__list" v-if="listData.length > 0"></div>
      <div class="resume-card__add" v-else>
        <Button type="primary" @click="$emit('new')">+ 添加{{title}}</Button>
      </div>
    </div>
  </card-wrap>
</template>

<script>
import cardWrap from "./card-wrap";
import { Button } from "vant";
export default {
  props: {
    title: String,
    listData: {
      type: Array,
      default: () => []
    }
  },
  components: {
    cardWrap,
    Button
  }
};
</script>

<style lang="postcss" scoped>
.resume-card {
  padding: 22px 20px 35px;
  &__list {
    margin-top: 20px;
  }
  &__add {
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
}
</style>



