<template>
  <div class="resume-home">
    <h1 class="resume-home__title">我的名片</h1>
    <card-wrap>
      <div class="resume-home__main clear">
        <img class="avatar" src="/resume-card/static/image/avatar.jpeg" />
        <div class="resume-home__main--content">
          <div class="name">maczyt</div>
          <div class="company">Home前端开发</div>
          <div class="work">IT|ahdkjhajkdh</div>
        </div>
        <img @click.stop="$router.push('/share')" class="qr-code" src="/resume-card/static/image/qr-code.png" />
      </div>
    </card-wrap>

    <card 
      title="工作经历"
      class="resume-home__work"
      @new="$router.push('/newWork')"
      ></card>

    <card 
      title="教育经历"
      class="resume-home__education"
      ></card>
  </div>
</template>

<script>
import { Search } from "vant";
import cardWrap from "@/components/card-wrap";
import card from "@/components/card";
export default {
  name: "home",
  components: {
    cardWrap,
    card,
    Search
  }
};
</script>

<style lang="postcss" scoped>
$blue: #f00;
.resume-home {
  overflow-x: hidden;
  overflow-y: auto;
  padding-left: 12.5px;
  padding-right: 12.5px;
  &__title {
    font-size: 25px;
    margin-left: 12.5px;
  }
  &__main {
    padding: 30px 20px;
    position: relative;
    margin-bottom: 10px;
    .avatar {
      float: left;
      width: 75px;
      height: 75px;
      border-radius: 50%;
    }
    .qr-code {
      position: absolute;
      right: 20px;
      top: 20px;
      width: 30px;
      height: 30px;
    }
    &--content {
      height: 70px;
      margin-left: 90px;
      display: flex;
      font-size: 14px;
      flex-direction: column;
      justify-content: space-around;
      .name {
        font-weight: bold;
        font-size: 20px;
      }
    }
  }
  &__work {
    margin-bottom: 10px;
  }
}
</style>




